import { Injectable } from "@angular/core";
import { HttpClient, HttpParams } from "@angular/common/http";
import { Observable } from "rxjs/internal/Observable";
import { PagedResponse } from "src/app/shared/models/shared/paged.response";
import {
  CreateAnalysisRequest,
  GetAnalysesRequest,
  GetAnalysesCountRequest,
} from "./analysis.input";
import { CountResponse } from "src/app/shared/models/shared/count.response";
import { Analysis } from "src/app/shared/models/analysis-management/analysis.model";

/**
 * Anlysis Service
 */
@Injectable()
export class AnalysisService {
  path = `v1/analysis-management/analyses`;
  constructor(private readonly http: HttpClient) {}

  /**
   * Get analyses
   */
  getAnalyses(input: GetAnalysesRequest): Observable<PagedResponse<Analysis>> {
    return this.http.get<PagedResponse<Analysis>>(`${this.path}`, {
      params: new HttpParams({ fromObject: input as any }),
    });
  }

  /**
   * Get analyses count
   */
  getAnalysesCount(input: GetAnalysesCountRequest): Observable<CountResponse> {
    return this.http.get<CountResponse>(`${this.path}/count`, {
      params: new HttpParams({ fromObject: input as any }),
    });
  }

  /**
   * Get analysis
   */
  getAnalysis(analysisId: string): Observable<Analysis> {
    return this.http.get<Analysis>(`${this.path}/${analysisId}`);
  }

  /**
   * Create analysis
   */
  createAnalysis(input: CreateAnalysisRequest): Observable<Analysis> {
    return this.http.post<Analysis>(`${this.path}`, input);
  }
}
