import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { LayoutComponent } from "./layout.component";

const routes: Routes = [
  {
    path: "",
    component: LayoutComponent,
    children: [
      {
        path: "",
        loadChildren: () =>
          import("../dashboard/dashboard.module").then(
            (m) => m.DashboardModule
          ),
      },
      // Analysis Management
      {
        path: "analysis-management/analyses",
        loadChildren: () =>
          import("../analysis-management/analyses/analyses.module").then(
            (m) => m.AnalysesModule
          ),
      },
      {
        path: "analysis-management/analysis-types",
        loadChildren: () =>
          import(
            "../analysis-management/analysis-types/analysis-types.module"
          ).then((m) => m.AnalysisTypesModule),
      },
      // Laboratory Management
      {
        path: "laboratory-management/value-references",
        loadChildren: () =>
          import(
            "../laboratory-management/value-references/value-references.module"
          ).then((m) => m.LaboratoryValueReferencesModule),
      },
      {
        path: "laboratory-management/emails",
        loadChildren: () =>
          import("../laboratory-management/emails/emails.module").then(
            (m) => m.LaboratoryEmailsModule
          ),
      },
      {
        path: "laboratory-management/phones",
        loadChildren: () =>
          import("../laboratory-management/phones/phones.module").then(
            (m) => m.LaboratoryPhonesModule
          ),
      },
      // Samples Management
      {
        path: "sample-management/samples",
        loadChildren: () =>
          import("../sample-management/samples/samples.module").then(
            (m) => m.SamplesModule
          ),
      },
      // Settings
      {
        path: "settings",
        loadChildren: () =>
          import("../settings/settings.module").then((m) => m.SettingsModule),
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class LayoutRoutingModule {}
