import { Component } from "@angular/core";
import { Store } from "@ngxs/store";
import { ToggleSidebarAction } from "src/app/store/sidebar/sidebar.actions";
import { Router } from "@angular/router";
import { LogoutAction } from "src/app/store/auth/auth.actions";
import { Observable } from "rxjs";
import { ToggleThemeAction } from "src/app/store/theme/theme.actions";
import { take } from "rxjs/operators";
import { Locale } from "src/app/shared/models/common/locale.model";

@Component({
  selector: "app-layout-header",
  templateUrl: "./header.component.html",
  styleUrls: ["./header.component.scss"],
})
export class HeaderComponent {
  headerTitle$: Observable<string>;
  locales: Locale[] = [
    { localeCode: "en-US", label: "English" },
    { localeCode: "ar-SA", label: "العربية" },
  ];

  constructor(private store: Store, private router: Router) {
    this.headerTitle$ = this.store.select((state) => state.header.title);
  }

  navbarToggle(): void {
    this.store.dispatch(new ToggleSidebarAction());
  }

  themeToggle(): void {
    this.store.dispatch(new ToggleThemeAction());
  }

  logout(): void {
    this.store
      .dispatch(new LogoutAction())
      .pipe(take(1))
      .subscribe(() => {
        this.router.navigate(["/auth/login"]);
      });
  }
}
