import { Component, OnInit } from "@angular/core";
import { Store } from "@ngxs/store";
import {
  ShowSidebarAction,
  HideSidebarAction,
} from "src/app/store/sidebar/sidebar.actions";
import { Observable } from "rxjs";
import { BreakpointObserver, BreakpointState } from "@angular/cdk/layout";

@Component({
  selector: "app-layout-sidebar",
  templateUrl: "./sidebar.component.html",
  styleUrls: ["./sidebar.component.scss"],
})
export class SidebarComponent implements OnInit {
  sidebarStatus$: Observable<boolean>;
  smallLayout = false;

  /**
   * Constructor
   */
  constructor(
    private breakpointObserver: BreakpointObserver,
    private store: Store
  ) {
    this.sidebarStatus$ = this.store.select((state) => state.sidebar.status);
  }

  /**
   * Initialize
   */
  ngOnInit(): void {
    this.registerBreakpointObserver();
  }

  registerBreakpointObserver(): void {
    this.breakpointObserver
      .observe(["(min-width: 599.99px)"])
      .subscribe((state: BreakpointState) => {
        this.smallLayout = !state.matches;
        this.store.dispatch(
          state.matches ? new ShowSidebarAction() : new HideSidebarAction()
        );
      });
  }

  /**
   * Toggle sidebar
   * This method is required to update the status in case the user clicks on other place
   * to hide the sidebar with mode equals to over
   */
  toggleSidebar(opened: boolean): void {
    this.store.dispatch(
      opened ? new ShowSidebarAction() : new HideSidebarAction()
    );
  }
}
