import { Component } from "@angular/core";
import { Store } from "@ngxs/store";
import { Router } from "@angular/router";
import { HeaderUpdateAction } from "src/app/store/header/header.actions";
import { HideSidebarAction } from "src/app/store/sidebar/sidebar.actions";

interface SidebarItem {
  name: string;
  link?: string;
  icon?: string;
  subItems?: SidebarItem[];
  divider: boolean;
}

@Component({
  selector: "app-layout-sidebar-content",
  templateUrl: "./sidebar-content.component.html",
  styleUrls: ["./sidebar-content.component.scss"],
})
export class SidebarContentComponent {
  items: SidebarItem[] = [
    {
      name: $localize`Dashboard`,
      link: "",
      icon: "dashboard",
      divider: false,
    },
    {
      name: $localize`Analysis Manager`,
      link: "analysis-management/analyses",
      icon: "science",
      divider: false,
    },
    {
      name: $localize`Sample Manager`,
      link: "sample-management/samples",
      icon: "label",
      divider: false,
    },
    {
      name: $localize`People Manager`,
      link: "people-management/persons",
      icon: "group",
      divider: false,
    },
    {
      name: $localize`Settings`,
      link: "settings",
      icon: "settings",
      divider: true,
    },
  ];

  constructor(private store: Store, private router: Router) {}

  /**
   * Get screen width
   */
  public get width() {
    return window.innerWidth;
  }

  /**
   * Navigate
   */
  navigate(item: SidebarItem): void {
    this.router.navigate([item.link]);
    if (this.width < 765) {
      this.store.dispatch(new HideSidebarAction());
    }
    this.store.dispatch(
      new HeaderUpdateAction({
        title: item.name,
      })
    );
  }
}
